import React, { useState, useEffect } from "react";
import Skeleton from "../../components/Skeleton/Skeleton.jsx";
import { Link } from "react-router-dom";
import s from "./ResultOfSearch.module.scss";
import Card from "../../components/Card/Card.jsx";

export default function ResultOfSearch({ search }) {
  const [movies, setMovies] = useState([]);
  let [isLoading, setIsLoading] = useState(true);
  useEffect(() => {
    function getMovies() {
      let protectedUrl = `https://api.kinopoisk.dev/v1.2/movie/search?query=${search}`;
      fetch(protectedUrl, {
        method: "GET",
        headers: {
          "X-API-KEY": process.env.REACT_APP_KEY,
        },
      })
        .then((resp) => resp.json())
        .then((data) => {
          setMovies(data.docs);
          setIsLoading(false);
        });
    }
    getMovies();
  }, [search]);
  console.log(movies);

  return isLoading === false ? (
    <section className={s.result}>
      <h1 className={s.result__title}>Результаты:</h1>
      {movies.length > 0 ? (
        <div className={s.result__wrapper}>
          <div className={s.result__cards}>
            <Card movies={movies} />
          </div>
        </div>
      ) : (
        <div>
          <p className={s.result__nothing}>Ничего не найдено...</p>{" "}
          <Link to="/" className={s.result__back}>
            Назад
          </Link>
        </div>
      )}
    </section>
  ) : (
    <Skeleton />
  );
}
