import React from "react";
import s from "./Card.module.scss";
import { Link } from "react-router-dom";
import noImg from "../../images/noImg.jpg";
export default function Card({ movies }) {
  return (
    <>
      {movies
        .filter((el) => el.description.length > 0 && el.name.length > 0)
        .map((el, index) => (
          <div className={s.card} key={index}>
            <div className={s.card__img}>
              <Link to={"/movie/" + el.id}>{el.poster === null ? <img src={noImg} alt="картинка" /> : <img src={el.poster} alt="картинка" />}</Link>
            </div>
            <div className={s.card__info}>
              <Link to={"/movie/" + el.id}>
                <h2 className={s.card__name}>{el.name}</h2>
                <p className={s.card__description}>{el.description}</p>
              </Link>
              <div className={s.card__year}>{el.year} г.</div>
            </div>
          </div>
        ))}
    </>
  );
}
